.form {
  
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  
}

form {
  width: 80%;
  min-width: 400px;
  max-width: 800px;
  max-height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: white;
  overflow: hidden;
  border-radius: 20px;
  padding: 20px;
}

form button {
  width: 125px;
  height: 50px;
  border: 0;
  border-radius: 15px;
  background-color: #001517;
  transition: 0.3s linear;
  color: white;
  font-weight: bold;
}

form button:hover {
  background-color: unset;
  border: 2px solid #fca17d;
  transition: 0.3s linear;
  color: black;
  cursor: pointer;
}

.formWord {
  width: 50%;
  padding: 2%;
}

.formWord input {
  width: 100%;
  height: 50px;
  margin-bottom: 10%;
  border: 0;
  margin-top: 2%;
  padding-left: 3%;
  background: #ffffff;
  box-shadow: 0px 2px 4px 0px rgb(218, 218, 218);
}
.formWord textarea {
  width: 100%;
  margin-bottom: 5%;
  border: 0;
  margin-top: 2%;
  height: 80px;
  color: black;
  box-shadow: 0px 2px 4px 0px rgb(218, 218, 218);
  padding-top: 3%;
  padding-left: 3%;
}

.submittedForm {
  width: 100%;
  padding-top: 1px;
  padding-bottom: 10px;
  color: #001517;
}

.sentMsg {
  margin-top: -400px;

}

@media screen and (max-width: 768px) {
  .formWord, input, button {
    font-size: 12px;
  };

  form{
    width: 60%;
    min-width: 200px;
    max-width: 400px;
    max-height: 400px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: white;
    overflow: hidden;
    border-radius: 20px;
    padding: 10px;
  };

 

}
.slider {
    width: 2000px;
    height: 200px;
    margin: auto;
    -webkit-animation: cambio 15s linear infinite;
            animation: cambio 15s linear infinite;
    overflow: visible;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    margin-bottom: 50px;
}
.slider ul {
    display: -webkit-flex;
    display: flex;
    padding: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.slider li {
    padding:0 20px;
    list-style: none;
}
.slider img {
    max-width: -webkit-fit-content !important;
    max-width: -moz-fit-content !important;
    max-width: fit-content !important;
    height: auto !important;
}

.logosImg {
    margin-top: 75px;
    max-width: -webkit-fit-content !important;
    max-width: -moz-fit-content !important;
    max-width: fit-content !important;
    height: auto !important;
    margin-left: 50px;
    
}

.logosImg2 {
    margin-top: 40px;
