.downloadBtn, .downloadBtn:visited {
    margin-top: -80px;
    margin-left: -80px;
    width: 125px;
    height: 50px;
    border: 0;
    border-radius: 15px;
    background-color: #001517;
    transition: 0.3s linear;
    color: hsl(347, 62%, 75%);
    font-weight: bold;
    text-decoration: none !important;
}

.downloadBtn a {
    text-decoration: none !important;
    color: hsl(347, 62%, 80%);
    height:20px;
    width: 50px;
}

.downloadBtn:hover {
    background-color: unset;
    border: 2px solid hsl(347, 62%, 75%);
    transition: 0.3s linear;
    color: black;
    cursor: pointer;
  }

  .downloadBtn a:hover {
    transition: 0.3s linear;
    color: hsl(347, 62%, 75%);
    cursor: pointer;
  }


@media screen and (max-width: 768px) {
    .downloadBtn {
    margin-top: 0px;
    margin-left: 0px;
    width: 75px;
    border-radius: 10px;
    };
}
.gallery-item {
    margin: 10px;
}

.gallery-item img{
    border-radius: 1%;
    padding-top: 50px;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: ease-in-out 0.4s;
    -webkit-animation: ease-in-out 0.4s;
    -moz-animation: ease-in-out 0.4s;
}

.gallery-item img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: ease-in-out 0.4s;
    -webkit-animation: 0.4s ease-in-out;
    -moz-animation: ease-in-out 0.4s;

    

}

@media screen and (max-width: 768px) {
        .gallery-item img:hover {
        -webkit-transform: none;
                transform: none;
        };
}
