.form {
  
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  
}

form {
  width: 80%;
  min-width: 400px;
  max-width: 800px;
  max-height: 500px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  background-color: white;
  overflow: hidden;
  border-radius: 20px;
  padding: 20px;
}

form button {
  width: 125px;
  height: 50px;
  border: 0;
  border-radius: 15px;
  background-color: #001517;
  -webkit-transition: 0.3s linear;
  -o-transition: 0.3s linear;
  transition: 0.3s linear;
  color: white;
  font-weight: bold;
}

form button:hover {
  background-color: unset;
  border: 2px solid #fca17d;
  -webkit-transition: 0.3s linear;
  -o-transition: 0.3s linear;
  transition: 0.3s linear;
  color: black;
  cursor: pointer;
}

.formWord {
  width: 50%;
  padding: 2%;
}

.formWord input {
  width: 100%;
  height: 50px;
  margin-bottom: 10%;
  border: 0;
  margin-top: 2%;
  padding-left: 3%;
  background: #ffffff;
  -webkit-box-shadow: 0px 0px 48px 0px rgb(218, 218, 218);
  box-shadow: 0px 2px 4px 0px rgb(218, 218, 218);
}
.formWord textarea {
  width: 100%;
  margin-bottom: 5%;
  border: 0;
  margin-top: 2%;
  height: 80px;
  color: black;
  -webkit-box-shadow: 0px 0px 8px 0px rgb(218, 218, 218);
  box-shadow: 0px 2px 4px 0px rgb(218, 218, 218);
  padding-top: 3%;
  padding-left: 3%;
}

.submittedForm {
  width: 100%;
  padding-top: 1px;
  padding-bottom: 10px;
  color: #001517;
}

.sentMsg {
  margin-top: -400px;

}

@media screen and (max-width: 768px) {
  .formWord, input, button {
    font-size: 12px;
  };

  form{
    width: 60%;
    min-width: 200px;
    max-width: 400px;
    max-height: 400px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    background-color: white;
    overflow: hidden;
    border-radius: 20px;
    padding: 10px;
  };

 

}